<!--交易记录-->
<template>
  <!--  组织机构使用样本-->
  <div class="new-tree1 new-tree2" v-if="type">
    <el-card class="new-card1 new-card2" shadow="always">
      <el-container style="width:100%; border: 1px solid #eee">
        <!--        default-expand-all-->
        <el-aside v-if="showTree" width="200px"
          style=" height: calc(100vh - 125px); background-color: rgb(238, 241, 246)">
          <el-tree style=" height: calc(100vh - 125px);" v-if="showTree" :data="treeData"
            :default-expanded-keys="defaultTreeKey" node-key="id" ref="org_tree" highlight-current
            :props=" {children: 'detail',label: 'caption' }" @node-click="handleTreeNodeClick"
            :render-content="initTreeDataStatus">
          </el-tree>
        </el-aside>
        <el-container style="width:100%; border: 1px solid #eee;">
          <el-aside width="25px" style="height: calc(100vh - 125px); background-color: rgb(238, 241, 246)">
            <i :class="icon1"
              style="height:100%;display: flex;width:25px;flex-direction: row;justify-content: center;align-items: center;"
              @click="showHiddenTree"></i>
          </el-aside>
          <el-main style="height: calc(100vh - 125px);padding-top:0px;overflow:hidden">
            <el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane label="优惠券活动" name="first">
                <div class="coment" v-if="activeName=='first'">
                  <componentTable class="component" v-for="(item, index) in couponTableList" :key="index" :msg='item'
                    :type="type" :list='list' />
                </div>
              </el-tab-pane>
              <el-tab-pane label="移动支付便民示范工程" name="second">
                <div class="coment" v-if="activeName=='second'">
                  <componentTable class="component" v-for="(item, index) in convenientPaymentTableList" :key="index"
                    :msg='item' :type="type" :list='list' />
                </div>
              </el-tab-pane>
              <el-tab-pane label="抽奖活动" name="third">
                <div class="coment" v-if="activeName=='third'">
                  <componentTable class="component" v-for="(item, index) in convenientPaymentList" :key="index"
                    :msg='item' :type="type" :list='list' />
                </div>
              </el-tab-pane>
            </el-tabs>
          </el-main>
        </el-container>
      </el-container>
    </el-card>
  </div>

  <div class="TransactionRecord" v-else>
    <div class="content">优惠券活动</div>
    <div class="box_c">
      <componentTable class="box_c_c" v-for="(item, index) in couponTableList" :key="index" :msg='item' :type="type" />
    </div>
    <div class="content">移动支付便民示范工程</div>
    <div class="box_c">
      <componentTable class="box_c_c" v-for="(item, index) in convenientPaymentTableList" :key="index" :msg='item'
        :type="type" />
    </div>
    <div class="content">抽奖活动</div>
    <div class="box_c">
      <componentTable class="box_c_c" v-for="(item, index) in convenientPaymentList" :key="index" :msg='item'
        :type="type" />
    </div>

  </div>
</template>

<script>
  import Bus from "../../../../utils/Bus.js"
  import componentTable from "./componentTable.vue";
  export default {
    name: "TransactionRecord",
    data() {
      return {
        activeName: 'first',
        list: { id: '0', type: '0' },
        type: false,
        couponTableList: [],
        convenientPaymentTableList: [],
        dialogFormVisible1: false,
        editableTabs: [],
        communityRows: [],
        cbInfo: {},//存储组织机构树中选择的小区及楼栋信息，当前值
        oldCbInfo: {},//存储组织机构树中选择的小区及楼栋信息，旧值
        dialogLeft: 0,
        dialogTop: 0,
        curTreeNode: null,
        curTreeData: null,
        icon1: "el-icon-caret-left",//"el-icon-d-arrow-left",
        showTree: true,
        currentSelTreeNodeId: '',//当前选择节点的ID
        currentSelTreeNodeIds: [],//当前选择节点的IDs,从父级到子级,为用户赋组织机构权限用
        treeDataStatus: {},//当前用户的组织机构树可显示的树节点集合
        treeData: [//机构树模板
          // {id:'0',caption:'数字营销平台',type:'0',detail:[
          //     {id:'100',caption:'中国银行行系_1',type:'1',detail:[
          //         {id:'100_1',caption:'中国银行省行_1',type:'2',detail:[
          //             {id:'100_1_1',caption:'中国银行市行_1',type:'3',detail:[
          //                 {id:'100_1_1_1',caption:'中国银行县区行_1',type:'4'},
          //                 {id:'100_1_1_2',caption:'中国银行县区行_1',type:'4'},
          //               ]},
          //             {id:'100_1_2',caption:'中国银行市行_2',type:'3'},
          //           ]},
          //         {id:'100_2',caption:'中国银行省行_2',type:'2'},
          //       ]},
          //     {id:'200',caption:'中国银行行系_2',type:'1',detail:[
          //         {id:'200_1',caption:'中国银行省行_2',type:'2'},
          //         {id:'200_2',caption:'中国银行省行_3',type:'2'},
          //       ]},
          //   ]}


        ],
        defaultTreeKey: []
      }
    },
    components: { componentTable },
    created() {
      this.couponTableList = [
        {
          name: '优惠券发放情况（领券数）',
          notes: '成功被领取的优惠券数量，反映了下级机构/网点的推广情况及客户参与情况。',
          api: 'postQueryGetTicketNumberReport',
          bank_center_id: 'bauti_bank_center_id',
          province_id: 'bauti_province_id',
          city_id: 'bauti_city_id',
          county_id: 'bauti_county_id',
          Company: '张',
        },
        {
          name: '优惠券使用情况（用券数）',
          notes: '客户使用的优惠券数量，反映了客户对网点所提供优惠券的满意度。',
          api: 'postQueryCheckTicketNumberReport',
          bank_center_id: 'bauti_bank_center_id',
          province_id: 'bauti_province_id',
          city_id: 'bauti_city_id',
          county_id: 'bauti_county_id',
          Company: '张',
        },
        {
          name: '客户领券数（以商户为单位统计）',
          notes: '客户领券数，反映了客户对商户期待值和喜好值。',
          api: 'postQueryGetTicketNumberReportForMer',
          query_company_id: 'query_company_id',
          cur_company_id: 'cur_company_id',
          type: '1',
          Company: '张',
        },
        {
          name: '客户到店数（以商户为单位统计）',
          notes: '客户到店数，反映了银行为商家的引流情况，数量越大，表明对商家的经营助力越大。',
          api: 'postQueryCheckTicketNumberReportForMer',
          query_company_id: 'query_company_id',
          cur_company_id: 'cur_company_id',
          type: '1',
          Company: '张',
        },
        {
          name: '信用卡激活人次',
          notes: '信用卡激活人次，即为使用微信绑定信用卡支付并成功领券人次。',
          api: 'postQueryGetDebitOrCreditNumberReport',
          bank_center_id: 'bauti_bank_center_id',
          province_id: 'bauti_province_id',
          city_id: 'bauti_city_id',
          county_id: 'bauti_county_id',
          flag: '2',
          Company: '人次',
        },
        {
          name: '储蓄卡激活人次',
          notes: '储蓄卡激活人次，即为使用微信绑定储蓄卡支付并成功领券人次。',
          api: 'postQueryGetDebitOrCreditNumberReport',
          bank_center_id: 'bauti_bank_center_id',
          province_id: 'bauti_province_id',
          city_id: 'bauti_city_id',
          county_id: 'bauti_county_id',
          flag: '1',
          Company: '人次',
        },
      ]
      this.convenientPaymentTableList = [
        {
          name: '支付笔数',
          notes: '支付笔数，反映了移动便民示范工程开展效果。',
          api: 'Total_payment_business_Kanban',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '笔',
        },
        {
          name: '支付金额',
          notes: '支付金额，反映了移动便民示范工程交易金额。',
          api: 'Payment_amount_Kanban',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '元',
        },
        {
          name: '特惠价参与人次',
          notes: '特惠价参与人次，即网点为推广手机银行、信用卡激活等给予客户的特惠价支付次数，反映了手机银行等业务的客户体验情况。一般指定客户参与，并限制单人参与次数。',
          api: 'Special_discount_participants',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '人次',
        },
        {
          name: '优惠价参与人次',
          notes: '优惠价参与人次=网点商品优惠价支付人次+入驻商户商品优惠价支付人次，优惠价为银行为客户提供的低于市场价购买的权益，一般不指定客户参与，并不限制数量。',
          api: 'Preferential_price_participants',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '人次',
        },
        {
          name: '服务费收入',
          notes: '服务费为商户在移动支付体验机中售卖的商品（含网点交付的和到店兑换的）向银行支付的营销服务费，网点可对入驻的商户设置每笔交易服务费所占比例。',
          api: 'Service_fee_income_amount',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '元',
        },

      ]
      this.convenientPaymentList = [
        {
          name: '抽奖活动人次',
          notes: '客户参与“抽福袋”功能活动人次。',
          api: 'Number_of_participants_in_lottery',
          bank_center_id: 'obj_bank_center_id',
          province_id: 'obj_province_id',
          city_id: 'obj_city_id',
          county_id: 'obj_county_id',
          Company: '人次',
        },
      ]
      this.initOrgTemplateInfo();
      this.type = this.customGlobal.userInfoExt.cui_type == '0' ? true : false
    },
    watch: {
      currentSelTreeNodeId: function (newVal, oldVal) {
        if (newVal !== oldVal) {
          Bus.$emit('globalMsgForCommunityBuilder', {
            community: { ci_uuid: '', ci_community_name: '' },
            builder: { bi_build_number: '', bi_uuid: '' }
          });
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      removeTreeNode(node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      initTreeDataStatus(h, { node, data, store }) {
        if (JSON.stringify(this.treeDataStatus) === "{}") {
          if (data.type === 4000) {
            return (
              <span style="font-size:14px;"  >
                <span >{data.caption}</span>
                <el-button type="text" size="mini" on-click={($event) => this.showOrgInfo(data, node, $event)}>选择</el-button>
              </span>
            )
          } else {
            return (
              <span style="font-size:14px;">
                <span>{data.caption}</span>
              </span>
            )
          }

        } else {
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if (data.id === k) {
              if (data.type === 4000) {
                return (
                  <span style="font-size:14px;"  >
                    <span  >{data.caption}</span>
                    <el-button type="text" size="mini" on-click={($event) => this.showOrgInfo(data, node, $event)}>选择</el-button>
                  </span>
                )
              } else {
                return (
                  <span style="font-size:14px;">
                    <span>{data.caption}</span>
                  </span>
                )
              }
              // console.info("两者匹配data.id:"+data.id+",k:"+k);
              find = true;
              break;
            } else {
              // console.info("两者不匹配data.id:"+data.id+",k:"+k);
            }
          }
          if (find === false) {
            // console.info("被删除，data.id不在k里:"+data.id);
            if (data.id === '0') {
              return (
                <span style="font-size:14px;">
                  <span>{data.caption}</span>
                </span>
              )
            } else {
              this.removeTreeNode(node, data);
            }

          }
        }
      },
      initOrgTemplateInfo() {
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify({ config: 'all', username: this.customGlobal.userInfoExt.cui_login_id }) })
        );

        p1.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            if (this.customGlobal.userInfoExt.cui_type === '0') {
              // if(JSON.parse(res.data.organ).detail.length>0){
              //   this.treeData = JSON.parse(res.data.organ).detail;//不带 数字营销平台0
              // }else{
              this.treeData = [JSON.parse(res.data.organ)];//带 数字营销平台0，

              // }

            } else {
              this.treeData = JSON.parse(res.data.organ).detail;//不带 数字营销平台0
            }

            this.treeDataStatus = JSON.parse(res.data.orgkey);

            console.info("treeData:" + JSON.stringify(this.treeData));
            console.info("treeDataStatus:" + JSON.stringify(this.treeDataStatus));
            // loading.close();

          } else {
            console.info("查询失败");
            if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              this.$router.replace({ path: '/login' });
            } else {
              this.treeData = [JSON.parse(res.data.organ)];
              this.treeDataStatus = JSON.parse(res.data.orgkey);
            }

            // loading.close();


          }


          //机构数处理
          getDeepArrData(this.treeData[0].detail);
          function getDeepArrData(arr) {
            for (var i = 0; arr && i < arr.length; i++) {
              let item = arr[i];
              if (Array.isArray(item.detail) && item.detail.length > 0) {
                // console.log('res==', item);
                getDeepArrData(item.detail);
              }
              if (item.type == '5') {
                arr.splice(i, 1)
                i--
              }
            }
          }

        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          // loading.close();
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          // loading.close();

          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

      },

      showOrgInfo(data, node, e) {
        // console.info("当前选择节点："+  this.$refs.org_tree.getCurrentKey() );
        // console.info("选择换热站："+JSON.stringify(data));
        // console.info("当前树节点："+this.currentSelTreeNodeId);
        if (this.$refs.org_tree.getCurrentKey() !== null && this.$refs.org_tree.getCurrentKey() === data.id) {
          // e.preventDefault();
          e.stopPropagation();
        }

        this.curTreeData = data;//缓存当前data
        this.curTreeNode = node;//缓存当前node

        this.dialogFormVisible1 = true;
        console.info("e.pageX :" + e.pageX);
        console.info("e.clientX :" + e.clientX);
        var rect = e.target.getBoundingClientRect();
        console.info("rect.left:" + rect.left);
        console.info("rect.top:" + rect.top);
        this.dialogLeft = rect.left + 10;
        this.dialogTop = rect.top / 2;

      },
      showHiddenTree() {

        if (this.showTree) {
          // this.icon1 = "el-icon-d-arrow-right";
          this.icon1 = "el-icon-caret-right";
          //this.windowWidth= (this.windowWidth+300);
        } else {
          // this.icon1 ="el-icon-d-arrow-left";
          this.icon1 = "el-icon-caret-left";
          //this.windowWidth = (this.customGlobal.win_w_h.width-300)+'px';
          //this.windowWidth= (this.windowWidth-300) ;
        }
        this.showTree = !this.showTree;

        console.info("是否显示：" + this.showTree);


      },
      handleTreeNodeClick(data, node) {

        //不是换热站时如果对话框显示则关闭
        if (data.type !== 4) {
          if (this.dialogFormVisible1 === true) {
            this.dialogFormVisible1 = false;
          }

        }
        this.list = data

        console.log('2点击事件,可在此处理对应逻辑_data:', data);
        //console.log("得到当前选择key_getCurrentKey："+this.$refs.org_tree.getCurrentKey());
        //console.log("得到当前选择key_getCurrentNode："+this.$refs.org_tree.getCurrentNode().caption);
        // console.log("得到当前选择key_getCurrentNode.parent："+this.$refs.org_tree.getNode( this.$refs.org_tree.getCurrentNode()).parent.label);
        let node_label = [];
        let node_key = [];
        let node_type = [];
        let node_obj = [];

        node_label.push(this.$refs.org_tree.getCurrentNode().caption);
        node_key.push(this.$refs.org_tree.getCurrentNode().id);
        node_type.push(this.$refs.org_tree.getCurrentNode().type + "");
        node_obj.push({ id: this.$refs.org_tree.getCurrentNode().id, caption: this.$refs.org_tree.getCurrentNode().caption, type: this.$refs.org_tree.getCurrentNode().type + "" });
        this.getTreeNodeParentCaption(node_label, node_key, node_type, node_obj, this.$refs.org_tree.getCurrentNode());
        //console.info("node_label:"+node_label.reverse());
        //console.info("node_key:"+node_key.reverse());
        let _level_ = this.$refs.org_tree.getNode(this.$refs.org_tree.getCurrentNode()).level;
        //if(_level_==5){
        // this.$message({
        //   type: 'error',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   message: "<spand style='color:black;'>不支持对["+this.$refs.org_tree.getCurrentNode().caption+"]层级进行操作</spand>"
        // });
        //}else{
        if (this.currentSelTreeNodeId !== this.$refs.org_tree.getCurrentKey()) {//选择的树节点有变化


          this.currentSelTreeNodeId = this.$refs.org_tree.getCurrentKey();
          for (var i1 = 0; i1 < node_label.length; i1++) {
            //node_label[i1] = encodeURIComponent(node_label[i1]);
            node_label[i1] = this.customGlobal.encrypt(node_label[i1], this.customGlobal.cryptoKey);
          }
          this.$router.push({
            name: 'org_query_bank', path: '/org_query_bank',
            query: {
              orgid: this.currentSelTreeNodeId,
              caption: node_label.reverse(),
              id: node_key.reverse(),
              cur_obj: { orgid: this.currentSelTreeNodeId, caption: this.$refs.org_tree.getCurrentNode().caption, type: this.$refs.org_tree.getCurrentNode().type + "" },
              type: node_type.reverse(),
              obj: node_obj.reverse(),
              cb_info: this.cbInfo,
            }
          })
          this.currentSelTreeNodeIds = node_key.reverse();
          console.info("2currentSelTreeNodeIds:" + this.currentSelTreeNodeIds);

        } else {//选择的树节点没变化 ,解决重复点击换热站不选择小区和楼栋时需要清空楼栋与小区

          Bus.$emit('globalMsgForCommunityBuilder', {
            community: { ci_uuid: "", ci_community_name: "" },
            builder: { bi_build_number: '', bi_uuid: '' }
          });
          this.cbInfo = {
            community: { ci_uuid: "", ci_community_name: "" },
            builder: { bi_build_number: '', bi_uuid: '' }
          };

          this.currentSelTreeNodeId = this.$refs.org_tree.getCurrentKey();
          for (var i1 = 0; i1 < node_label.length; i1++) {
            //node_label[i1] = encodeURIComponent(node_label[i1]);
            node_label[i1] = this.customGlobal.encrypt(node_label[i1], this.customGlobal.cryptoKey);
          }
          this.$router.push({
            name: 'org_query_bank', path: '/org_query_bank',
            query: {
              orgid: this.currentSelTreeNodeId,
              caption: node_label.reverse(),
              id: node_key.reverse(),
              cur_obj: { orgid: this.currentSelTreeNodeId, caption: this.$refs.org_tree.getCurrentNode().caption, type: this.$refs.org_tree.getCurrentNode().type + "" },
              type: node_type.reverse(),
              obj: node_obj.reverse(),
              cb_info: this.cbInfo,
            }
          })
          this.currentSelTreeNodeIds = node_key.reverse();
          console.info("3currentSelTreeNodeIds:" + this.currentSelTreeNodeIds);

        }
        // }

      },
      //返回树节点层级caption
      getTreeNodeParentCaption(label, key, type, obj, node) {
        let currentNode = this.$refs.org_tree.getNode(node);
        let len = currentNode.level;
        let loop1 = 1;
        // console.info("level:"+len);
        while (loop1 < len) {
          if (currentNode.parent !== undefined) {
            label.push(currentNode.parent.label);
            key.push(currentNode.parent.key);

            type.push(currentNode.parent.data.type + "");
            obj.push({ id: currentNode.parent.key, caption: currentNode.parent.label, type: currentNode.parent.data.type + "" });

            currentNode = this.$refs.org_tree.getNode(currentNode.parent);
            loop1++;
          }
        }
        return { "label": label, "key": key, "type": type, "obj": obj };


      },


    }
  }
</script>

<style scoped>
  .new-tree1 /deep/ .el-tree {
    width: 100% !important;
    overflow: auto !important;
  }

  .new-tree1 /deep/ .el-tabs__item {
    width: auto !important;
  }

  .new-tree2 /deep/ .el-tree>.el-tree-node {
    display: inline-block !important;
    min-width: 100% !important;
  }


  .new-card1 /deep/ .el-card__header {
    /*background:#f5f5f5 !important;*/
    background: #f2f3f8 !important;
    /*height: 10px!important;*/
    padding: 10px !important;
  }

  .new-card2 /deep/ .el-card__body {
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    padding-left: 5px !important;
    padding-right: 5px !important;
  }

  .coment {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: calc(100vh - 175px);
    padding: 0px 5px;
    overflow-y: scroll;
    box-sizing: border-box;
  }

  .coment::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 5px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }

  .coment::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 10px;
    background-color: rgb(221, 222, 224);
  }

  .coment::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: inset 0 0 5px #ffffff;
    /* background   : ; */
    border-radius: 10px;
  }

  .component {
    margin-top: 15px;
    width: 49%;
    min-width: 480px;
  }

  .content {
    font-size: 28px;
    font-weight: 700;
    text-align: left;
    margin: 30px 40px 10px;
    white-space: nowrap;
  }

  .box_c {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 40px;
    box-sizing: border-box;
  }

  .box_c_c {
    margin-top: 15px;
    width: 49%;
    min-width: 600px;
  }
</style>


